.home-image{
    height: 60vh;
    object-fit: cover;
    object-position: 40% 0%;
}

.home-background{
    position: relative;
}

.home-background:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 500px;
    background: linear-gradient(0, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.475));
 }

 .title-home{
    position: absolute;
    top: 15vh;
    left:5vh;
 }


 @media (min-width: 768px)  {
    .home-image{
        height: 60vh;
        object-fit: cover;
        object-position: 40% 0%;
    }

    .title-home{
        position: absolute;
        top: 20vw;
        left:7vw;
     }
}

@media (min-width: 992px)  {
    .home-image{
        height: 100vh;
    }
}