.footer-container{
    background-color: var(--primary);
    padding: 20px;
}

.footer-body{
    text-align: center;
    color: var(--white);
}

.privacy{
    text-decoration: underline;
}

.footer-logo{
    height: 80px;
    padding-top: 0px;
}


@media (min-width: 992px)  {
    .footer-logo{
        height: 100px;
        float: right;
        position: absolute;
        right: 20px;
        padding-top: 20px;
    }
}
