.navigation-container {
    position: absolute;
    z-index: 2;
    top:0px;
    margin-top: 0px;
    padding-top: 10px;
    width: 98vw;
}

.scrolled-down.navigation-container {
    position: fixed;
    top:0;
    padding-top: 0px;
    background-color: var(--background);
    transition: all .2s ease-in;
    transform-origin: left top;
}

.open.navigation-container {
    position: fixed;
    opacity: 0.98;
    top:0px;
    background-color:var(--white);
    height: 100vh

}

.logo{
    width: 30px;
    height: 40px;
    margin: 5px 5px 5px 15px;
    content:url("../../assets/logos/Logo Matthew 3 white.png");
    transition: all 0.5s;
}

.scrolled-down .logo{
    content:url("../../assets/logos/Logo w Border Text.png");
}

.open .logo{
    content:url("../../assets/logos/Logo w Border Text.png");
}


.menu-toggle {
    margin: 22px 30px 15px 15px;
    float: right;
    width: 1.6rem;
    height: 1.2rem;
    left: 2.1rem;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
  
.line {
    width: 100%;
    height: 2px;
    background-color: var(--white);
    transition: all 0.5s;
}

.scrolled-down .line{
    background-color: black;
}
.open .line {
    background-color: black;
}
.open .line-1 {
    transform: rotate(-46deg) translate(-5px, 4px);
}
  
.open .line-3 {
    transform: rotate(46deg) translate(-4.5px, -4px);
}
  
.open .line-2 {
    opacity: 0;
}

.menu-items{
    margin-top: 0;
    display: flex;
    flex-direction: column;
    visibility: hidden;
    height: 0;
    opacity: 0;
    gap: 40px;
    transition: visibility 0s, opacity 0.5s linear;
    text-align: center;
}

.open .menu-items{
    visibility: visible;
    opacity: 1;
    height: auto;
    margin-top: 20vh;
    
}

a{
    
    text-decoration: none;
    list-style: none;
    font-size: 15px;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
}



@media (min-width: 768px)  {
    .logo{
        width: 37.5px;
        height: 50px;
    }

    a{
        font-size: 18px;
        
    }

}

@media (min-width: 992px)  {

    .navigation-container {
        display: flex;
        flex-direction: row;
        height: 70px;
        margin-top: 0px;
    }

    .logo{
        position: absolute;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        width: 45px;
        height: 60px;
    }

    .scrolled-down a{
        color: black;
    }

    .scrolled-down .logo{
        position:relative;
        margin: 5px 5px 5px 15px;
    }

    .menu-toggle {
        display: none;
    }

    .menu-items{
        width: 100%;
        height: auto;
        margin-top: 20px;
        visibility: visible;
        opacity: 1;
        flex-direction: row;
        justify-content: center;
        color: white;
        text-align: center;
        margin-left: 40px;

    }

    a{
        font-size: 12px;
        line-height: 3em;
        color: white;
    }

    .projecten{
        margin-right: 30px;
    }

    .contact{
        margin-left: 30px;
    }

    .scrolled-down .projecten{
        margin-right: 0px;
    }

    .scrolled-down .contact{
        margin-left: 0px;
    }
    
    .home{
        display: none;
    }
}

