.contact-container{
    background-color: var(--primary-dark);
    padding: 50px !important;
}

.contact-sub-container{
    color: var(--white) !important;
}

.contact-sub-container h3{
    color: var(--white) !important;
}

.info-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    text-decoration: none;
    color: white;
    list-style: none;
    font-size: 15px;
    font-weight:normal;
    letter-spacing: 1px;
    text-transform:inherit;
    font-style: normal;
}

.contact-body{
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    margin: auto;
}

.icons{
    margin: 20px auto 20px auto;
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.contact-container .button{
    float: right;
}

@media (min-width: 992px)  {

    .contact-body{
        flex-direction: row;
        gap: 0px;
        width: 70%;
        margin-top: 40px;
    }

    .contact-side{
        width: 40%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 0 auto auto auto;
    }
}