:root {
  --white: #ffffff;
  --text: #000000f0;
  --background: #f4f4f4;
  --primary: #99c2d8;
  --primary-dark: #44ace4;
  --secondary: #e2532b;
  --secondary-fade:#e2532b81;

}

body {
  margin: 0;
  font-family:  -apple-system, BlinkMacSystemFont , 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  font-family: 'Roboto', sans-serif;
}

h1 {
  color: var(--white);
  font-size: 30px;
  font-weight: bold;
  margin: 0;
}

h2 {
  color: var(--white);
  font-size: 16px;
  font-weight: 100;
  margin: 0;
}

h3 {
  color: var(--text);
  font-size: 40px;
  font-weight:300;
  margin: 0 0 1rem 0;
  text-align: center;
}

.container{
  padding: 100px;
}

.icon{
  width: 30px;
  fill: white;
  vertical-align: middle;
}

.form-label{
  font-size: 15px;
  font-weight: bold;
}

.required{
  color: var(--secondary);
}

.form-input{
  border-radius: 8px;
  border-color: var(--white);
  border-width: 0px;
  background-color: white;
  width: 100%;
  height: 4vh;
  margin:5px 0 15px 0;
  font-family: 'Roboto', sans-serif;

}

.form-textarea{
  border-radius: 8px;
  border-color: var(--white);
  border-width: 0px;
  width: 100%;
  height: 25vh;
  margin:5px 0 15px 0;
  font-family: 'Roboto', sans-serif;
}

.button {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: .25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  display: inline-flex;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 1.25;
  margin: 0;
  min-height: 3rem;
  padding: calc(.875rem - 1px) calc(1.5rem - 1px);
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
}

.button:hover,
.button:focus {
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
  color: rgba(0, 0, 0, 0.85);
}

.button:hover {
  transform: translateY(-1px);
}

.button:active {
  background-color: #F0F0F1;
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
  color: rgba(0, 0, 0, 0.65);
  transform: translateY(0);
}



@media (min-width: 768px)  {
  h1 {
    font-size: 40px;
  }
  
  h2 {
    font-size: 20px;
  }
}

@media (min-width: 992px)  {
  h1 {
    font-size: 5vw;
  }
  
  h2 {
    font-size: 1.5vw;
  }
}