.aboutus-container{
    background-color: var(--primary);
    padding: 50px !important;
}

.aboutus-sub-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 50px;
    margin: auto;

}

.profile-picture{
    height: 300px;
    border-radius: 20px;
    vertical-align: middle;
    margin: auto;
}


.aboutus-text-container{
    display: flex;
    flex-direction: column;
    gap: 0px;
    vertical-align: middle;
    margin: auto;
}


@media (min-width: 768px)  {
    .aboutus-sub-container{
        flex-direction: row !important;
    }
    
    .profile-picture{
        height: 300px;
    }
}

@media (min-width: 992px)  {
    .profile-picture{
        height: 400px;
        border-radius: 20px;
    }

    .aboutus-text-container{
        flex-direction: row;
        gap: 50px;
    }
}

@media (min-width: 1280px)  {
    .aboutus-sub-container{
        width: 70%;
    }
}
