.mission-container{
    background-color: var(--background);
}

.misson-sub-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 50px;
    margin: auto;
}

.mission-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.mission-icon{
    height: 200px;
    vertical-align:middle;
}

@media (min-width: 768px)  {
    .misson-sub-container{
        padding: 50px !important;
        flex-direction: row;
        width: 60%;
    }


    .mission-icon{
        height: 150px;
    }
}

@media (min-width: 992px)  {

    .mission-icon{
        height: 230px;
    }

}